import '@babel/polyfill';

import '../../../res/layui/layui'
import '../../../res/layui/css/layui.css'
import '../../../res/static/css/main.css'
import '../../../pages/public/css/footer.css'
import '../../../pages/public/css/header.less'
import {
	animation,
	goTo,
	goBack
} from './util'

layui.config({
	dir: '/dist/static/lib/'
})

layui.define(['jquery', 'layer'], function() {
	var $ = layui.jquery,
		layer = layui.layer;
		
		/**头部单击显示下划线 */
		var str = window.location.href;
		str = str.substring(str.lastIndexOf("/") + 1);
		str = str.substring(0, str.lastIndexOf("."));
		$(`.active-${str}`).css({
			'border-bottom': '2px solid #808082'
		})
	//监听页面用户是否登陆
	$(document).ready(function() {
		 if(str !== 'index'){
			 if (!sessionStorage.getItem("token")) {
			 	layer.confirm('当前未登陆，跳转到登陆页面', {
			 		btn: ['确定'],
			 		title: '提示',
			 		closeBtn: 0,
			 		btn1: function(index, layero) {
			 			window.location.href = "index.html"
			 		},
			 	});
				$("body,.main").height($(window).height()).css({
				  "overflow-y": "hidden"
				});
			 }
		 }
	});



	// 监听状态栏滚动条事件（状态栏随滚动条）
	window.addEventListener('scroll', function() {
		var t = $('body, html').scrollTop(); // 目前监听整个body的滚动条距离
		var h = $('.header_box').height()
		if (t > h) {
			$('.header_box').addClass('box-active')
		} else {
			$('.header_box').removeClass('box-active');
		}
	});

	//解决移动端头部显示在pc端
	window.addEventListener('resize', () => {
		if (animation.getClientWidth() > 780) {
			$('.header-toggle').css({
				'transform': 'translateX(300%)'
			})
			$('.shade').removeClass('global-shade')
		}
	})

	/*   遮罩切换跳转   */
	$('.app-header-menuicon').on('click', () => goTo($('.header-toggle')));

	/*  移动端 头部返回  */
	$('.go_back').click(() => goBack($('.header-toggle')))

});
