import "@babel/polyfill";
import '../../res/layui/layui'
import '../../res/layui/css/layui.css'
import '../../res/static/css/main.css'
import '../../res/static/css/animate.css'
import '../../res/static/js/public'
import './solution.css'
import './solution'
$(document).ready(function() {
        // 点击图标显示左侧垂直导航栏并且显示遮挡层
        $('#showLag').click(function() {

            $('.layHide').css("display", "block");
            $('.site-mobile').css("display", "block");
            $('.site-mobile-shade').css("display", "block")
        });

        // 点击遮挡层 隐藏遮挡层
        $('.site-mobile-shade').click(function() {
            $('.layHide').css("display", "none");
            // $('.site-mobile').css("display", "none");
            $('.site-mobile-shade').css("display", "none")
        });
})
    // 加载layui模块
layui.use(['carousel', 'element'], function() {
    var carousel = layui.carousel,
        $ = layui.$;

    //建造实例
    carousel.render({
        elem: '#test1',
        width: '100%' //设置容器宽度
            ,
        arrow: 'always',
        height: 'auto'
            //,anim: 'updown' //切换动画方式
    });
});